<template>
  <section class="week">
    <div class="week__btns">
      <button class="week__btn" :class="{active: change}" @click="change = !change">На неделю</button>
      <button class="week__btn" @click="getWeather('Tashkent'), change = false">Отменить</button>
    </div>
    <div class="week__days" :class="{active: change}">
      <PerDay :i="i" :day="day" v-for="(day, i) in getDailyWeather" :key="i"/>
    </div>
  </section>
</template>

<script>

import {mapGetters, mapActions} from 'vuex'
import PerDay from '@/components/Week/PerDay.vue'

export default {
  components: {
    PerDay
  },
  data(){
    return {
      change: false
    }
  },
  computed: {
    ...mapGetters(['getDailyWeather'])
  },
  methods: {
    ...mapActions(['getWeather'])
  }
}
</script>

