<template>
  <div class="mainContent">
    <div class="mainContent__left">
      <h1 class="mainContent__left-title">{{Math.round(getFullWeather.current.temp)}}°</h1>
      <h2 class="mainContent__left-today">Сегодня</h2>
      <p class="mainContent__left-time" >Время: <span v-if="time">{{time}}</span> </p>
      <p class="mainContent__left-city">Город: {{getFullWeather.name}}</p>
      <div class="mainContent__left-img">
        <img src="@/assets/images/clear.svg" alt="" v-if="description == 'ясно'" />
        
        <img src="@/assets/images/rain.svg" alt="" v-else-if="description == 'дождь'" />
        <img src="@/assets/images/small_rain.svg" alt="" v-else-if="description == 'небольшой дождь и солнце'" />
        <img src="@/assets/images/small_rain_sun.svg" alt="" v-else-if="description == 'небольшой дождь'"/>
        <img src="@/assets/images/mainly_cloudy.svg" alt="" v-else />
      </div>
      <img src="@/assets/images/bg.svg" alt="" class="mainContent__left-bg" />
    </div>
    <div class="mainContent__right">
      
      <img src="@/assets/images/bg.svg" alt="" class="mainContent__right-cloud" />
      
      <div class="mainContent__right-item">
        <div class="mainContent__right-icon">
          <img src="@/assets/images/temp.svg" alt="" />
        </div>
        <span class="mainContent__right-title">Температура</span>
        <span class="mainContent__right-desc">{{Math.round(getFullWeather.current.temp)}}° - ощущается как {{Math.round(getFullWeather.current.feels_like
)}}°</span>
      </div>
      <div class="mainContent__right-item">
        <div class="mainContent__right-icon">
          <img src="@/assets/images/pressure.svg" alt="" />
        </div>
        <span class="mainContent__right-title">Давление</span>
        <span class="mainContent__right-desc">{{getFullWeather.current.pressure}} мм ртутного столба - нормальное</span>
      </div>
      <div class="mainContent__right-item">
        <div class="mainContent__right-icon">
          <img src="@/assets/images/osadki.svg" alt="" />
        </div>
        <span class="mainContent__right-title">Осадки</span>
        <span class="mainContent__right-desc">{{getFullWeather.current.humidity}} %</span>
      </div>
      <div class="mainContent__right-item">
        <div class="mainContent__right-icon">
          <img src="@/assets/images/wind.svg" alt="" />
        </div>
        <span class="mainContent__right-title">Ветер</span>
        <span class="mainContent__right-desc">{{Math.round(getFullWeather.current.wind_speed)}} м/с {{getWind}} - {{getPower}} ветер</span>
      </div>
    </div>
  </div>
</template>

<script>

import {mapState, mapActions, mapGetters} from 'vuex'

export default {
  data(){
    return {
      time: null
    }
  },
  computed: {
    ...mapGetters(['getFullWeather']),
    description(){
      return this.getFullWeather.current.weather[0].description
    },
    getWind(){
      if(this.getFullWeather.current.wind_deg == 0 && this.getFullWeather.current.wind_deg == 360 ){
        return 'север'
      }
      else if(this.getFullWeather.current.wind_deg > 0 && this.getFullWeather.current.wind_deg < 90){
        return 'северо-восток'
      }
      else if(this.getFullWeather.current.wind_deg == 90){
        return 'восток'
      }
      else if(this.getFullWeather.current.wind_deg > 90 && this.getFullWeather.current.wind_deg < 180){
        return 'юго-восток'
      }
      else if(this.getFullWeather.current.wind_deg == 180){
        return 'юг'
      }
      else if(this.getFullWeather.current.wind_deg > 180 && this.getFullWeather.current.wind_deg < 270 ){
        return 'югo-запад'
      }
      else if(this.getFullWeather.current.wind_deg == 270 ){
        return 'запад'
      }
      else if(this.getFullWeather.current.wind_deg > 270 && this.getFullWeather.current.wind_deg < 360){
        return 'северо-запад'
      }
    },
    getPower(){
      if(this.getFullWeather.current.wind_speed < 5){
        return 'слабый'
      }
      else if(this.getFullWeather.current.wind_speed >= 5 && this.getFullWeather.current.wind_speed < 10){
        return 'умеренный'
      }
      else{
        return 'сильный'
      }
    }
  },
  methods: {
    getTime() {
      const currentTime = new Date(
        Date.now() + this.getFullWeather.timezone_offset * 1000
      );
      
      const utcHours =
        currentTime.getUTCHours() < 10
          ? "0" + currentTime.getUTCHours()
          : currentTime.getUTCHours();
      const utcMinutes =
        currentTime.getUTCMinutes() < 10
          ? "0" + currentTime.getUTCMinutes()
          : currentTime.getUTCMinutes();
      this.time = `${utcHours}:${utcMinutes}`;
    },
  },
  mounted() {
    this.getTime();
    setInterval(this.getTime, 1000); // Update time every second
  },
};
</script>
